.paperWrapper {
  border-radius: 5px;
  box-shadow: 1px 1px;
  color: gray;
  margin: 2% 10%;
  padding: 4%;
  display: flex;
  flex-direction: column;
  background: white;
}

.topContainer {
  display: flex;
  flex-direction: column;
}

.bottomContainer {
  display: flex;
  flex-direction: row; 
}
.blockLeft {
  width: 60%;
  display: flex;
  flex-direction: column;
  padding-right: 2%;
}
.blockRight {
  width: 40%;
}

.fullname {
  color: black;
  font-family: 'Courier New', Courier, monospace;
}
.briefIntro {
  color: black;
  font-family: 'Courier New', Courier, monospace;
  font-size: 1.2em;
}
.link {
  color: #ec407a;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.itemContainer {
  color: black;
  margin: 20px 0px;
}
.itemTitle {
  text-transform: uppercase;
  margin: 10px 2px;
  font-size: 16px;
  font-weight: 500;
}
.itemContent {
  margin: 5px 2px;
}
.skillContainer {
  display: flex;
  flex-wrap: wrap;
  /* flex-direction: column; */
}
.skillItem {
  max-width: 50%;
  width: 50%;
  display: flex;
  flex-direction: row;
  margin: 5px 0px;
}
.skillImage {
  width: 40px;
  height: 40px;
  margin: 5px;
}
.skillName {
  margin-left: 5px;
}